body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
    background-color: #1abc9c;
}

.App{
    width: 800px;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: yellow;
}

.App-form{
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 8px 4px;
    padding-bottom: 16px;
    border-bottom: 1px solid #aaa;
}

.App-form > :nth-child(1){
    display: flex;
    flex-direction: column;
}

.App-form > :nth-child(1) > * {
    outline: none;
    border: 1px solid #aaa;
    transition: all .3s;
    border-radius: 2px;
}

.App-form > :nth-child(1) > *:focus{
    box-shadow: 0 0 7px #1abc9c;
    border: 1px solid #1abc9c;
}

.App-form > :nth-child(1) > :first-child{
    font-size: 18px;
    height: 24px;
    padding: 2px 8px;
}

.App-form > :nth-child(1) > :last-child {
    margin-top: 4px;
    font-size: 16px;
    height: 40px;
    padding: 2px 8px;
  }
  
  .App-form > :nth-child(2) {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .App-form button {
    border: 1px solid #ccc;
    background: #f3ccf3;
    padding: 4px 8px;
    border-radius: 2px;
    margin-top: 8px;
    cursor: pointer;
    box-shadow: 0px 2px 2px 0px rgba(0,0,0,.1);
  }
  
  .App-form button:hover {
    box-shadow: 0px 2px 2px 2px rgba(0,0,0,.1);
  }

.ToDoListItem {
    border: 1px solid aquamarine;
    margin: 12px;
    border-radius: 4px;
    width: 300px;
    background-color: #fafbfd;
    box-shadow: 1px 2px 5px 3px rgba(0,0,0,.1);
    padding: 4px 2px;
}
  
.ToDoListItem-title {
    font-size: 18px;
    margin: 0 8px 4px;
    border-bottom: 1px solid #333;
    text-align: left;
    padding: 4px 8px;
}
  
.ToDoListItem-description {
    word-wrap: break-word;
    padding: 8px;
}
